@import url(https://fonts.googleapis.com/css?family=Teko:700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.flip-root{
  height: 500px;
  width: 100vw;
}
.card_list {
  background-color: #212121;
  height: 100vh;
  width: 100vw;
}

body {
  background: #212121;
  color: white;
  font-size: 27px;
  font-family: 'Teko';
  overflow: hidden;
}

.form-control, .form-control:hover, .form-control:focus, .btn, .btn:hover, .btn:focus {
  color: white;
  height: 50px;
  background: #212121;
  border: 1px solid white;
  margin: 5px;
  outline: 0;
}

.box {
  background: green;
  color: floralwhite;
  box-shadow: 0 8px 16px 0 rgba(134, 80, 80, 0.2);
}

.fixed {
  position: absolute;
  bottom: 10px;
  -webkit-transform: translate(-50.5%, 0);
          transform: translate(-50.5%, 0);
  width: 100vw;
}

.my_container {
  height: 100vh;
}
